import { Button, Card, CardContent } from '@mui/material';
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import PieChart from '../common/components/PieChart';
import "./style/Result.css"

/**
*   Result
*/
const ResultPage = () => {
    const location = useLocation();

    const { result } = location.state;

    const data = [
        { label: "", value: result.numCorrect },
        { label: "", value: result.numIncorrect }
    ];

    return (
        <div>
            <h1> !</h1>
            <section>
                <div className='info'>
                    <Card sx={{ minWidth: 100, backgroundColor: "#1565c0", color: "white", padding: "1rem" }}>
                        <CardContent>
                            <ul>
                                <li> {result.quiz.length}</li>
                                <li> : {result.time}</li>
                                <li>: {result.numCorrect}</li>
                                <li>: {result.numIncorrect}</li>
                            </ul>
                        </CardContent>
                    </Card>
                </div>
            </section>
            <div className='chart-wrapper'>
                <PieChart data={data} />
            </div>

            <div className='button'>
                <Link to="/" style={{ textDecorationLine: "none" }}>
                    <Button variant='contained'>
                         
                    </Button>
                </Link>
            </div>

        </div>
    );
}

export default ResultPage;
