import React from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import ReviewQuizCard from './components/ReviewQuizCard';
import PieChart from '../common/components/PieChart';
import { Card, CardContent } from '@mui/material';
import "./style/Review.css"

/**
*  Review Detail
*/
const ReviewDeatilPage = () => {
    const location = useLocation()
    const navigate = useNavigate();
    const { result, index } = location.state
    const data = [
        { label: "", value: result.numCorrect },
        { label: "", value: result.numIncorrect }
    ];

    return (
        <div>
            <h1>{index}  </h1>
            <section>
                <div className='review info'>
                    <Card sx={{ minWidth: 100, backgroundColor: "#1565c0", color: "white", padding: "1rem" }}>
                        <CardContent>
                            <ul>
                                <li> {result.quiz.length}</li>
                                <li> : {result.time}</li>
                                <li>: {result.numCorrect}</li>
                                <li>: {result.numIncorrect}</li>
                            </ul>
                        </CardContent>
                    </Card>
                </div>
            </section>
            <section>
                <div className='review quizlist'>
                    <ul>
                        {result.quiz.map((e, i) => {
                            return <ReviewQuizCard result={e} index={i} key={i.toString()} />
                        })}
                    </ul>
                </div>
            </section>
            <section>
                <div className='review chart-wrapper'>
                    <PieChart data={data} />
                </div>
            </section>
            <div className='review button-group'>
                <button onClick={() => { navigate(-1) }}>
                     
                </button>

                <Link to="/" style={{ textDecorationLine: "none" }}>
                    <button>
                         
                    </button>
                </Link>
            </div>
        </div>
    );
}

export default ReviewDeatilPage;
