import { Button, Card } from '@mui/material';
import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { AlertContext } from '../../context/AlertContext';
import { QuizContext } from "../../context/QuizContext"
import { ResultsContext } from '../../context/ResultsContext';
import { usePrompt } from '../../hooks/useBlocker';
import useTimer from '../../hooks/useTimer';
import CorrectModal from './components/CorrectModal';
import QuizCard from './components/QuizCard';
import './style/Quiz.css'

/**
*   QuizPage
*/
const QuizPage = () => {
    const navigate = useNavigate();
    const { quiz, saveQuiz } = useContext(QuizContext);
    const { results, saveResults } = useContext(ResultsContext)
    const { alertProps, setAlertProps } = useContext(AlertContext);

    //    
    usePrompt('  ?', true);

    // 
    const { minutes, seconds } = useTimer();

    //   
    const [quizNumber, setQuizNumber] = useState(0)

    // 
    const [openModal, setOpenModal] = useState(false);

    //  
    const [selectedAnswer, setSelectedAnswer] = useState(0);

    // 
    const [result, setResult] = useState({
        quiz: [],
        numCorrect: 0,
        numIncorrect: 0,
        time: ""
    })

    /**
     *    
     * @param {boolean}  
     * @returns 
     */
    const nextQuiz = (isCorrect) => {
        setOpenModal(false);
        setSelectedAnswer(0)
        let newResult;
        let newQuiz = {
            ...quiz[quizNumber],
            choose_answer: quiz[quizNumber].all_answers[selectedAnswer]
        }
        if (isCorrect) {
            newResult = {
                ...result,
                quiz: [
                    ...result.quiz,
                    newQuiz
                ],
                numCorrect: result.numCorrect + 1,
            };
        } else {
            newResult = {
                ...result,
                quiz: [
                    ...result.quiz,
                    newQuiz,
                ],
                numIncorrect: result.numIncorrect + 1,
            };
        }

        if (quizNumber >= quiz.length - 1) {
            moveResult(newResult);
        } else {
            setResult(newResult);
            setQuizNumber(quizNumber + 1);
        }
    }

    /**
     *   
     * @param {Result} result 
     */
    const moveResult = (result) => {
        result = {
            ...result,
            time: `${minutes} ${seconds}`
        }
        setAlertProps({
            ...alertProps,
            show: true,
            title: "   ! ",
            contents: "     !",
            closeText: " ",
            clickClose: () => {
                setAlertProps({
                    show: false
                })
                saveResults([
                    ...results,
                    result
                ])
                saveQuiz([])
                navigate('/results', {
                    replace: true,
                    state: {
                        result: result
                    },
                })
            }
        })
    }

    /**
     *    
     * @returns {boolean}
     */
    const checkAnswer = () => {
        const { all_answers, correct_answer } = quiz[quizNumber];
        if (all_answers[selectedAnswer] === correct_answer) {
            return true;
        }
        return false;
    }

    /**
     * 
     * @param {number} m: 
     * @param {number} s: 
     * @returns {JSX}  
     */
    const Timer = ({ m, s }) => {
        return (
            <div className='timer'>
                <Card sx={{ minWidth: 100, backgroundColor: "#1565c0", color: "white", padding: "1rem" }}>{m} {s}</Card>
            </div>
        )
    }

    return (
        <>
            <div className='quizpage' data-testid="quizpage">
                <Timer m={minutes} s={seconds} />
                <QuizCard
                    quiz={quiz[quizNumber]}
                    quizNumber={quizNumber}
                    selectedAnswer={selectedAnswer}
                    setSelectedAnswer={setSelectedAnswer} />
                <Button variant='contained' onClick={() => setOpenModal(true)}></Button>
            </div>
            <CorrectModal openModal={openModal} checkAnswer={checkAnswer} nextQuiz={nextQuiz} />
        </>
    );
}

export default QuizPage;
